import { Button, Container, Grid } from "@mui/material";
import React, { useState } from "react";
import { BottomButton } from "../components/BottomButton";
import { CameraCard } from "../components/CameraCard";
import CustomDialog from "../components/CustomDialog";
import { Title } from "../components/Title";
import { UserName } from "../components/UserName";
import OcrCard from "./CardForOcr";

function StorageSearch({menu, userName, setMenu}:{menu:number, userName:string, setMenu:React.Dispatch<React.SetStateAction<number>>}) {
  const [openSuccess, setOpenSuccess ] = useState(false);
  const [openFailure, setOpenFailure ] = useState(false);

  const [num1, setNum1 ] = useState("");
  const [num1_ai, setNum1_ai ] = useState("");

  const [ reset, setReset ] = useState(false);

  const target = "YbhhGYuu";

  function runSearch (){
    if (num1 === target){
      setOpenSuccess(true);
    } else {
      setOpenFailure(true);
    }
  }

  return (
    <>
      <Title></Title>
      <UserName>{userName}</UserName>
      <Container component = "main" maxWidth = "xs">
        <Grid container
          rowSpacing = {1}
          display = "flex"
          direction = "column"
          >
          <Grid item>
              <OcrCard setNumber = {setNum1} setNumberPredicted = {setNum1_ai} reset = {reset}>/</OcrCard>
            </Grid>
          <Grid item>
            <Button
              variant = "contained"
              fullWidth
              size = "large"
              onClick = {() => runSearch()}>
                
            </Button>
          </Grid>
        </Grid>
      </Container>
      <CustomDialog
          title = ""
          text= "XXX"
          buttonText = ""
          // flagOnClose
          onClick = {()=>{
            setReset(!reset);
            setOpenSuccess(false);
          }}
          open = {openSuccess}
        />
        <CustomDialog
          title = ""
          text = ''
          buttonText = ""
          // flagOnClose
          onClick = {()=>{
            setOpenFailure(false);
          }}
          open = {openFailure}
        />
      <BottomButton onClick = {() => setMenu(0)}>TOP MENU</BottomButton>
    </>
    );
  }
  export default StorageSearch;